@import 'mixins';
@import 'variables';

.header {
  height: 100vh;
  background-image: url(/src/images/bike_header.png);
  background-size: cover;
  background-position: 90%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: nowrap;
  overflow-x: hidden;

  &__bottom {
    padding-bottom: 40px;

    @include content-padding-inline;
  }

  &__title {
    @media (min-width: 344px) {
      height: 36px;
      margin-bottom: 88px;
      justify-content: center;
      font-weight: 700px;
      font-size: 36px;
      line-height: 36px;
      color: $colorWhite;
      display: flex;
    }
  }
    @media (min-width: 576px) {
      & {
        height: 100vh;
        background-image: url(/src/images/bike_header.png);
        background-size: cover;
        background-position: center;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        white-space: nowrap;
      }

      &__title {
      height: 64px;
      margin-bottom: 130px;
      font-weight: 700px;
      font-size: 64px;
      line-height: 64px;
      color: $colorWhite;
      justify-content: center;
    }
  }

  @media (min-width: 1200px) {
    & {
      height: 100vh;
      background-image: url(/src/images/bike_header.png);
      background-size: cover;
      background-position: center;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      white-space: nowrap;
    }

    &__title {
      height: 80px;
      margin-bottom: 203px;
      font-weight: 700px;
      font-size: 80px;
      line-height: 80px;
      color: $colorWhite;
      text-align: center;
      justify-content: center;
    }
  }
}
